"use strict";

var d3 = require('d3');

var forceBoundry2 = require('./forceBoundry')["default"];

module.exports = {
  setupSimulation: setupSimulation,
  setupSimulationForces: setupSimulationForces
};

function setupSimulation(state) {
  var simulation = d3.forceSimulation();
  setupSimulationForces(simulation, state);
  return simulation;
}

function setupSimulationForces(simulation, state) {
  var nodes = Object.values(state.nodes);
  var links = Object.values(state.links);
  var width = state.container.width || 0;
  var height = state.container.height || 0;
  simulation.nodes(nodes) // // pull nodes along links
  .force('link', d3.forceLink().id(function (d) {
    return d.id;
  }).links(links).distance(function (d) {
    return d.distance;
  })) // push nodes away from each other
  .force('charge', d3.forceManyBody().strength(function (d) {
    return -4 * d.radius;
  })) // .force('collision', d3.forceCollide().radius(d => 1.2 * d.radius))
  // // translate nodes around the center
  .force('center', d3.forceCenter(width / 2, height / 2)) // // push nodes towards the center
  .force('x', d3.forceX(width / 2, 1)).force('y', d3.forceY(height / 2, 1)) // push nodes back into frame
  // .force('boundry', forceBoundry2(0, 0, width, height))
  // warm then cool
  .alpha(1).alphaTarget(0).restart();
}

function createForcePerNode(forceFn) {
  return createForce(function (nodes, alpha) {
    for (var index = 0; index < nodes.length; index++) {
      var node = nodes[index];
      forceFn(node, alpha);
    }
  });
}

function createForce(forceFn) {
  var nodes;

  var result = function result(alpha) {
    forceFn(nodes, alpha);
  };

  result.initialize = function (_nodes) {
    nodes = _nodes;
  };

  return result;
}