var h = require('react-hyperscript');

var s = require('react-hyperscript');

var renderBaseGraph = require('./base');

module.exports = renderGraph;

function renderGraph(state, actions) {
  return renderBaseGraph(state, actions, {
    renderNode: renderNode,
    renderLink: renderLink
  });

  function renderNode(node, state, actions) {
    var selectedNode = state.selectedNode,
        networkState = state.networkState;
    var isSelected = selectedNode === node.id;
    var color = node.color;
    var normalRadius = node.radius;
    var radius = isSelected ? normalRadius * 2 : normalRadius;
    var label = node.label || node.id;
    return s('circle', {
      r: radius,
      fill: color,
      cx: node.x,
      cy: node.y,
      onClick: function onClick() {
        return actions.selectNode(node.id);
      }
    }, [s('title', "".concat(label))]);
  }

  function renderLink(link, state, actions) {
    var source = link.source,
        target = link.target;
    return s('line', {
      strokeWidth: link.value,
      x1: source.x,
      y1: source.y,
      x2: target.x,
      y2: target.y
    });
  }
}